
import { firebaseConfig } from '@/firebase/config';
import { initializeApp, getApps, getApp, type FirebaseApp } from 'firebase/app';
import { getAuth } from 'firebase/auth';
import { getFirestore } from 'firebase/firestore';

import {
  initializeApp as initializeAdminApp,
  getApps as getAdminApps,
  getApp as getAdminApp,
} from 'firebase-admin/app';
import { getAuth as getAdminAuth } from 'firebase-admin/auth';
import { getFirestore as getAdminFirestore } from 'firebase-admin/firestore';

const isServer = typeof window === 'undefined';

let clientApp: FirebaseApp;

function getClientServices() {
  if (!getApps().length) {
    try {
      clientApp = initializeApp(firebaseConfig);
    } catch (e) {
      // In a real app, you'd want more robust error handling
      console.error('Firebase client initialization failed:', e);
      // Fallback to getApp() if initialization was attempted elsewhere
      clientApp = getApp();
    }
  } else {
    clientApp = getApp();
  }
  return {
    firebaseApp: clientApp,
    auth: getAuth(clientApp),
    firestore: getFirestore(clientApp),
  };
}

function getAdminServices() {
  if (!getAdminApps().length) {
    initializeAdminApp();
  }
  const adminApp = getAdminApp();
  return {
    firebaseApp: adminApp, // Note: This is an AdminApp
    auth: getAdminAuth(adminApp),
    firestore: getAdminFirestore(adminApp),
  };
}

export function initializeFirebase() {
  if (isServer) {
    return getAdminServices();
  }
  return getClientServices();
}

// Barrel file exports
export * from './provider';
export * from './client-provider';
export * from './firestore/use-collection';
export * from './firestore/use-doc';
export * from './non-blocking-updates';
